<template>
  <div>
    <br />
    <el-page-header @back="goBack" content="详情页面"></el-page-header>
    <br />
    <el-card class="box-card">
      <h1>{{items.title}}</h1>
      <p>{{items.source}}</p>
      <p>{{items.ptime}}</p>
    </el-card>
    <br />
    <el-card class="box-card">
      <P v-html="items.body">{{items.body}}</P>
      <br />
      <p>{{items.statement}}</p>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: {}
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var id = this.$route.params.id;
      console.log(id)
      var api =process.env.VUE_APP_API + "/nc/article/" + id + "/full.html";
      this.axios.get(api).then(
        response => {
          if (response !== null) {
            console.log(response.data);
            this.items = response.data[id];
          }
        },
        err => {
          console.log(err);
        }
      );  
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>